import React, { useState, useEffect } from 'react';
import styled, { css } from 'styled-components';
import { Header, Footer } from '../components/common';
import Comment from '../components/comment/Comment';
import { tagStyle } from '../styles/mixins';
import axios from 'axios';

const Map = css`
  margin-top: ${(props) => props.theme.margin3};
  width: 100%;
  height: 250px;
`;

const Wrapper = styled.div`
  strong {
    font-weight: bold;
  }

  .place-detail-wrap {
    padding: 0 10%;

    .place-inner {
      margin-top: ${(props) => props.theme.margin1};
      display: flex;
      justify-content: space-between;

      img {
        width: 70%;
      }

      > .place-desc {
        margin-left: ${(props) => props.theme.margin2};

        > .place-category {
          ${tagStyle}
        }
        > .place-name {
          font-weight: bold;
          font-size: 30px;

          &:after {
            content: '';
            display: block;
            height: 1px;
            width: 65px;
            background: ${(props) => props.theme.subColor};
            margin-top: ${(props) => props.theme.margin3};
          }
        }
        > .place-info-box {
          margin-top: ${(props) => props.theme.margin3};
        }

        #map-overview {
          ${Map}
        }
      }
    }

    .comment-inner {
      margin-top: ${(props) => props.theme.margin2};
    }
  }
`;

const { kakao } = window;

const PlaceDetail = ({ location }) => {
  const place = location.state;
  const [detailInfo, setDetailInfo] = useState([]);

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_SERVER_URL}/api/place/${place.id}`)
      .then((response) => {
        setDetailInfo(response.data);
      });
  }, []);

  const MapContainer = () => {
    useEffect(() => {
      // ì§€ë„ë¥¼ í‘œì‹œí•  div
      const container = document.getElementById('map-overview');
      const options = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        lever: 3,
      };
      // ì§€ë„ ìƒì„±
      const map = new kakao.maps.Map(container, options);
      // ì£¼ì†Œ-ì¢Œí‘œ ë³€í™˜ ê°ì²´ ìƒì„±
      const geocoder = new kakao.maps.services.Geocoder();

      // ì£¼ì†Œë¡œ ì¢Œí‘œë¥¼ ê²€ìƒ‰
      geocoder.addressSearch(detailInfo.address, function (result, status) {
        // ê²€ìƒ‰ì´ ì •ìƒì ìœ¼ë¡œ ì™„ë£Œë˜ë©´
        if (status === kakao.maps.services.Status.OK) {
          const coords = new kakao.maps.LatLng(result[0].y, result[0].x);
          // ê²°ê³¼ê°’ìœ¼ë¡œ ë°›ì€ ìœ„ì¹˜ë¥¼ ë§ˆì»¤ì— í‘œì‹œí•¨
          var marker = new kakao.maps.Marker({
            map: map,
            position: coords,
          });

          map.setCenter(coords);
        }
      });
    }, []);

    return <div id='map-overview'></div>;
  };

  return (
    <Wrapper>
      <Header />
      <div className='place-detail-wrap'>
        <div className='place-inner'>
          <img src={place.imgUrl} alt={place.name} title={place.name}></img>
          <div className='place-desc'>
            <span className='place-category'>{place.category}</span>
            <h1 className='place-name'>{place.name}</h1>
            <div className='place-info-box'>
              <strong className='place-info'>íŠ¹ì§•</strong>
              <p>{detailInfo.contents}</p>
              <strong className='place-info'>ì£¼ì†Œ</strong>
              <p>{detailInfo.address}</p>
            </div>
            <MapContainer />
          </div>
        </div>
        <div className='comment-inner'>
          <strong>ì¥ì†Œ í•œì¤„í‰ ë‚¨ê¸°ê¸°</strong>
          <Comment />
        </div>
      </div>
      <Footer />
    </Wrapper>
  );
};

export default PlaceDetail;
