import React, { useCallback, useRef, useState } from 'react';
import styled from 'styled-components';
import CommentInput from './CommentInput';
import CommentList from './CommentList';

const Wrapper = styled.div`
  margin-top: ${(props) => props.theme.margin3};
`;

const Comment = () => {
  const [comments, setComments] = useState([
    {
      id: 1,
      contents: 'ì™€! ë„ˆë¬´ ëŒ€ë°•ì´ì—ìš”!',
    },
    {
      id: 2,
      contents: 'ì¹œêµ¬ê°€ ë°ë ¤ê°€ì¤¬ëŠ”ë° ì¢‹ì•˜ì–´ìš”!',
    },
    {
      id: 3,
      contents: 'ì €ëŠ” ì§ì›ì´ ë¶ˆì¹œì ˆí•´ì„œ ë³„ë¡œì˜€ìŠµë‹ˆë‹¤;;',
    },
  ]);

  // ê³ ìœ³ê°’ìœ¼ë¡œ ì‚¬ìš©ë  id
  // refë¥¼ ì‚¬ìš©í•˜ì—¬ ë³€ìˆ˜ ë‹´ê¸°
  const nextId = useRef(4);

  // ëŒ“ê¸€ ì¶”ê°€ ê¸°ëŠ¥
  const onInput = useCallback(
    (contents) => {
      const comment = {
        id: nextId.current,
        contents,
      };
      setComments(comments.concat(comment));
      nextId.current += 1;
    },
    [comments]
  );
  // ëŒ“ê¸€ ì‚­ì œ ê¸°ëŠ¥
  const onRemove = useCallback(
    (id) => {
      setComments(comments.filter((comment) => comment.id !== id));
    },
    [comments]
  );
  // ëŒ“ê¸€ ìˆ˜ì • ê¸°ëŠ¥
  const onUpdate = useCallback((id, contents) => {
    setComments(
      comments.map((comment) =>
        id === comment.id ? { ...comment, ...contents } : comment
      )
    );
  }, []);

  return (
    <Wrapper>
      <CommentInput onInput={onInput} />
      <CommentList
        comments={comments}
        onRemove={onRemove}
        onUpdate={onUpdate}
      />
    </Wrapper>
  );
};

export default Comment;
