import React from 'react';
import GoogleLogin from 'react-google-login';
import axios from 'axios';
import { useHistory } from 'react-router-dom';

const GoogleButton = (props) => {
  const history = useHistory();
  const onSuccess = async (response) => {
    const {
      googleId,
      profileObj: { email, name, imageUrl },
    } = response;
    try {
      // íšŒì› ê°€ì… ìœ ì € í™•ì¸ì„ ìœ„í•œ ì¡°íšŒ
      const res = await axios.get(
        `${process.env.REACT_APP_SERVER_URL}/api/user/${googleId}`
      );
      // íšŒì› í…Œì´ë¸”ì— ì—†ìœ¼ë©´ íšŒì› ê°€ì…í•˜ê¸°
      if (!res.id) {
        const userData = {
          id: googleId,
          email,
          name,
          imgUrl: imageUrl,
        };
        await axios.post(
          `${process.env.REACT_APP_SERVER_URL}/api/user`,
          userData
        );
      }
      // ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì— ìœ ì € ì •ë³´ ì €ì¥
      localStorage.setItem('USER_TOKEN', googleId);
      props.close();
    } catch (e) {
      console.log('ìœ ì € ì •ë³´ ì¡°íšŒ ì—ëŸ¬');
      console.error(e);
    }
  };

  const onFailure = (error) => {
    console.log(error);
  };

  return (
    <div>
      <GoogleLogin
        clientId={`${process.env.REACT_APP_GOOGLE_ID}`}
        responseType={'id_token'}
        onSuccess={onSuccess}
        onFailure={onFailure}
      />
    </div>
  );
};

export default GoogleButton;
