import React from 'react';
import styled from 'styled-components';
import { btnStyle } from '../../styles/mixins';

const Nav = styled.nav`
  margin-top: ${(props) => props.theme.margin1};
  display: flex;
  justify-content: center;

  .categories {
    cursor: pointer;
    float: left;
    ${btnStyle};

    &:hover {
      color: white;
    }
  }

  li + li {
    margin-left: ${(props) => props.theme.margin3};
  }

  &:after {
    content: '';
    display: block;
    clear: both;
  }
`;

const CategoryNav = (props) => {
  const handleChange = (e) => {
    if (e.target.innerHTML === 'ì‹ë‹¹') {
      return props.onToggle('res');
    }
    if (e.target.innerHTML === 'ì¹´í˜') {
      return props.onToggle('cafe');
    }
    if (e.target.innerHTML === 'ì†Œí’ˆìƒµ') {
      return props.onToggle('shop');
    }
    if (e.target.innerHTML === 'ì „ì²´') {
      return props.onToggle('all');
    }
  };

  return (
    <Nav>
      <ul onClick={handleChange}>
        <li className='categories'>ì „ì²´</li>
        <li className='categories'>ì‹ë‹¹</li>
        <li className='categories'>ì¹´í˜</li>
        <li className='categories'>ì†Œí’ˆìƒµ</li>
      </ul>
    </Nav>
  );
};

export default CategoryNav;
